@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;700&display=swap);
body{margin:0;font-family:'Work Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;min-height:100vh;color:#fff}#root{min-height:100vh;background-image:url(/instaweather/static/media/Background.027cc150.jpg);background-size:cover}.loader-wrapper{height:100vh;width:100vw;background-color:#ffffff52;display:block;align-items:center;justify-content:center;position:fixed;z-index:9999;top:0}.loader-wrapper .loader{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:50px;height:50px;background-color:#0f3043;border-radius:50%}.loader-wrapper .loader:after{content:'';position:absolute;border-radius:50%;top:50%;left:50%;border:0 solid white;transform:translate(-50%, -50%);animation:loading 1000ms ease-out forwards infinite;transition:all 0.3s ease}@keyframes loading{0%{border:0 solid white;transition:all 0.3s ease}20%{border:8px solid white;width:0;height:0;transition:all 0.3s ease}100%{border:8px solid white;width:100%;height:100%;transition:all 0.3s ease}}main{padding-top:40px}h1{font-size:144px;line-height:168px}h2{font-size:64px;line-height:75px}h3{font-size:48px;line-height:56px}h4{font-weight:bold;font-size:36px;line-height:42px}.border-bottom{border-color:rgba(255,255,255,0.5) !important}button.btn{text-transform:capitalize;padding:10px 30px;color:#fff}.btn,p{font-size:24px;line-height:28px}span{font-size:20px}::-webkit-scrollbar{width:0px !important;height:6px}::-webkit-scrollbar-track{background-color:transparent;border-radius:6px}::-webkit-scrollbar-thumb{background:#fff;border-radius:8px}::-webkit-scrollbar-corner{display:none}header{padding-top:40px;display:flex;justify-content:space-between;align-items:center}header button.btn{border-radius:0}header button.btn.active{border-left:1px solid #fff;background:rgba(255,255,255,0.25)}header button.btn.active:hover{border-left:1px solid #fff;background:rgba(255,255,255,0.25)}main{margin-top:170px;padding-bottom:150px}main .btn{font-size:22px}main .btn.active{border-bottom:1px solid #fff;font-weight:700}main .btn:hover{color:#fff;border-bottom:1px solid #fff;font-weight:700}.hourly-section{display:flex;flex-direction:column}.hourly-section img{max-width:65px;margin-bottom:10px}.hourly-section+.hourly-section{margin-left:70px}@media (max-width: 420px){header{flex-wrap:wrap}main{margin-top:70px}}

